.reload-btn{
    position:fixed;
    right:2rem;
    bottom:3rem;
    width:3rem;
    height:3rem;
    box-shadow: 2px 2px 3px solid gray;
    border-radius:50%;
    background:rgb(233, 37, 119);
    cursor: pointer;
}

@media only screen and (max-width:450px){
    .reload-btn{
        cursor:none;
    }
}